    methods:{
        cardClick(Keypath) {

            let num=parseInt(Keypath)
            console.log(num)
            if(num<500&&num>=400)
                this.$router.push({path: '/proto2/'+Keypath})
            if(num>=500)
                this.$router.push({path: '/proto/'+Keypath})
        },
    }
}
